import {
  follow, requestUsers, setCurrentPage, unfollow,
} from '../../Redux/users-reducer'
import { connect } from 'react-redux'
import React, { useEffect } from 'react'
import Users from './Users'
import Preloader from '../Common/Preloader/Preloader'
import { getCount, getCurrentPage, getFetching, getFollowingInProgress, getPageSize, getPortionSize, getUsers } from '../../Redux/usersSelectors.js'


const UsersContainer = ({
  users, pageSize, count, portionSize,
  currentPage, isFetching, followingInProgress, follow, unfollow, setCurrentPage, getUsers,
}) => {
  
  useEffect(() => {
    getUsers(currentPage, pageSize)
  }, [ currentPage, pageSize ])
  
  return <>
    { isFetching
        ? <Preloader />
        : <Users users = { users }
            count = { count }
            portionSize = { portionSize }
            currentPage = { currentPage }
            pageSize = { pageSize }
            followingInProgress = { followingInProgress }
            userFollow = { follow }
            userUnfollow = { unfollow }
            onPageChanged = { setCurrentPage }
        /> }
  </>
  
}

let mapStateToProps = (state) => {
  return {
    users: getUsers(state),
    pageSize: getPageSize(state),
    count: getCount(state),
    portionSize: getPortionSize(state),
    currentPage: getCurrentPage(state),
    isFetching: getFetching(state),
    followingInProgress: getFollowingInProgress(state),
    
  }
}

export default connect(mapStateToProps, {
  follow,
  unfollow,
  setCurrentPage,
  getUsers: requestUsers,
})(UsersContainer)

