import classes from './Users.module.scss'
import User from './User/User'
import Paginator from '../Common/Paginator/Paginator.jsx'


const Users = ({
  currentPage, count, pageSize,
  onPageChanged, portionSize, users, userFollow, userUnfollow, followingInProgress,
}) => {
  
  return (<div className = { classes.wrapper }>
    <div className = { classes.title }>Users</div>
    <Paginator totalItemCount = { count } pageSize = { pageSize }
        portionSize = { portionSize }
        currentPage = { currentPage } onPageChanged = { onPageChanged } />
    
    { users.map(u => <User key = { u.id }
        user = { u }
        userFollow = { userFollow }
        userUnfollow = { userUnfollow }
        followingInProgress = { followingInProgress }
    />) }
  </div>)
}

export default Users