import classes from './User.module.scss'
import userPhoto from '../../../assets/image/User.png'
import { NavLink } from 'react-router-dom'


const User = ({ user,followingInProgress,userUnfollow,userFollow }) => {
  
  return (<div className = { classes.wrapper }>
    <div className = { classes.ava }>
      <NavLink to = { '/profile/' + user.id }
          className = { classes.user_link }>
        { user.photos.small
            ? <img src = { user.photos.small } alt = 'ava' />
            : <img src = { userPhoto } alt = 'ava' /> }</NavLink>
      { user.followed
          ? <button
              disabled = { followingInProgress.some (id => id === user.id) }
              onClick = { () => userUnfollow (user.id) }>unfollow</button>
          : <button
              disabled = { followingInProgress.some (id => id === user.id) }
              onClick = { () => userFollow (user.id) }>follow</button> }
    </div>
    <div className = { classes.description }>
      <div className = { classes.user }>
        <div className = { classes.fullname }>{ user.name }</div>
        <div
            className = { classes.status }>{ user.status
            ? user.status
            : 'No status' }</div>
      </div>
      <div className = { classes.location }>
        <div
            className = { classes.city }>{ 'props.location.city' }<span>,</span>
        </div>
        <div className = { classes.country }>{ 'props.location.country' }</div>
      </div>
    </div>
  </div>)
}

export default User
